<template>
    <div v-if="ready">
        <div class='col-12' style="display:flex;flex-direction:row;">
            <div class='col-3' style='padding-left:0;margin-bottom:1em'>
                <label>Department</label>
                <select class='form-control select' v-model="dept" @change="new_data">
                    <option v-for="d in dept_list" :value="d.dept_id">{{ d.full_dept }}</option>
                </select>
            </div>            
            <div class='col-2' style='padding-right:0'>
                <label>Fundings</label>
                <select v-model="fund" class='form-control select' @change="new_data">
                    <option value='*'>All</option>
                    <option v-for="f in fundlist">{{ f.name }}</option>
                </select>
            </div>
            <div class='col-3'></div>
            <div class='col-2' style='padding-left:0'>
                <label>School Year</label>
                <input type="number" v-model="st" class="form-control" @change="update_period"/>
            </div>
            <div class='col-2' style='padding-left:0'>
                <label>&nbsp;</label>
                <input type="number" v-model="ed" class="form-control" readonly/>
            </div>
            <!--
            <div class="col-2" style='padding-left:0;padding-right:0'>
                <label>Live Search</label>
                <input type="text" class="form-control" placeholder="Enter Search Key" v-model="key" @keyup="live_new_data"/>
            </div>-->
        </div>
        <div class='col-12' v-html="print_list">
            {{ print_list }}
        </div>
    </div>    
</template>
<script>
export default {
    data(){
        return{
            fundlist:[],
            fund:'*',
            key:'',
            st:'',
            ed:'',
            dept:'',
            dept_list:[],
            ready:false,
            print_list:'',
        }
    },
async created(){
    await axios.get('Procurement/shared_backend/department.php')
        .then(
            (response)=>{
                this.dept_list = response.data;
                this.dept = this.dept_list[0].dept_id;
            }
        )
    await axios.get('Procurement/shared_backend/default_period.php')
        .then(
            (response)=>{
                var st = response.data.st.split("-");
                var ed = response.data.ed.split("-");
                this.st = st[0];
                this.ed = parseInt(ed[0]);
            }
        )
    var formData = new FormData();
    formData.append('dept',this.dept);
    formData.append('st',this.st);
    formData.append('ed',this.ed);
    await axios.post('Procurement/report/backend/dept_fund.php',formData)
        .then(
            (response)=>{
                this.fundlist = response.data;
            }
        )
    formData.append('fund',this.fund);
    await axios.post('Procurement/report/backend/asset_fetch.php',formData)
        .then(
            (response)=>{
                this.print_list = response.data;
            }
        )    
this.ready = true;
},
methods:{
    async update_period(){
        this.ed = parseInt(this.st)+1;
        this.$forceUpdate();
        await this.new_data();
    },
    async new_fund(){
        var formData = new FormData();
        formData.append('dept',this.dept);
        formData.append('st',this.st);
        formData.append('ed',this.ed);
        await axios.post('Procurement/report/backend/dept_fund.php',formData)
            .then(
                (response)=>{
                    this.fundlist = response.data;
                }
            )
    },
    new_data(){
    var formData = new FormData();
        formData.append('dept',this.dept);
        formData.append('st',this.st);
        formData.append('ed',this.ed);
        formData.append('fund',this.fund);
        axios.post('Procurement/report/backend/asset_fetch.php',formData)
            .then(
                (response)=>{
                    this.print_list = response.data;
                    this.key='';
                }
            )  
            this.new_fund();
    },
    live_new_data(){
        var formData = new FormData();
            formData.append('key',this.key);
            formData.append('dept',this.dept);
            formData.append('st',this.st);
            formData.append('ed',this.ed);
            formData.append('fund',this.fund);
            axios.post('Procurement/report/backend/asset_fetch.php',formData)
                .then(
                    (response)=>{
                        this.print_list = response.data;
                    }
                )   
    }
}
}
</script>



